
import React, { useState, useEffect, } from "react";
import axios from "axios";
import {
  CCard,
  CCardBody,
  CCol,
  CCardHeader,
  CButton,
  CFormInput,
  CFormLabel,
  CForm,
} from "@coreui/react";
import { useParams } from "react-router-dom";
import Swal from "sweetalert2";
import { CBreadcrumb, CBreadcrumbItem } from '@coreui/react'
import { useNavigate } from "react-router-dom";
// or

const EditState = () => {
    const { id } = useParams();
  const token = typeof window !== "undefined" ? localStorage.getItem("status") : null;
  const Toast = Swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", Swal.stopTimer);
      toast.addEventListener("mouseleave", Swal.resumeTimer); 
    },
  });

  const navigate = useNavigate();
  const [singleState, setSingleState] = useState("");

  // useEffect(() => {
  //   axios
  //     .get("stateList" + id, { headers: { Authorization: "Bearer " + token } })
  //     .then((res) => {
  //       setSingleState(res.data.data.name);
  //     });
  // }, [id, token]);

  useEffect(() => {
    axios
      .get("stateDetail/" +id, { headers: { Authorization: "Bearer " + token } })
      .then((res) => {
        console.log("state", res.data.data.id);
        console.log("state", res);
        setSingleState(res.data.data.state_name);
      });
    }, [token]);



  const handleSubmit = async (e) => {
    e.preventDefault();
    const helpdetailsobj = new FormData();
    helpdetailsobj.append("state_name", singleState);
    helpdetailsobj.append("id", id);

    await axios
      .post("addEditState" , helpdetailsobj, {
        
        headers: {
          Accept: "application/json",
          'Authorization': 'Bearer '+ token
        },
      })
      .then((result) => {
        console.log(result);

      //  result.data.status == 400
      //     ? Toast.fire({
      //         icon: "error",
      //         title: result.data.message,
      //       })
      //     : 
          Toast.fire({
              icon: "success",
              title: result.data.message,
            });

        result.data.status == 400 ? navigate("/edit-state/:id") : navigate("/state");
        
       
      })
      .catch((error) => {
        Toast.fire({
          icon: "error",
          title: error.response.data.message,
        });
      });
  };

  // useEffect(() => {
  //   if (localStorage.getItem("status")) {
  //     navigate("/state");
  //   }
  // }, [navigate]);


  return (
    <>
      
      <div className="px-2">
      <CCol xs={12} >
          <CCard className="p-2  mb-3">
          <CBreadcrumb className="m-0 ms-2">
      <CBreadcrumbItem href="/dashboard" style={{textDecoration:"none !important"}}>Dashboard</CBreadcrumbItem>
     
          <CBreadcrumbItem  href="/state">
           State
          </CBreadcrumbItem>
          <CBreadcrumbItem active>Edit State</CBreadcrumbItem>
  
    </CBreadcrumb>
          </CCard>
        </CCol>
     
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
              <strong>Edit State</strong>
            </CCardHeader>
            <CCardBody>
              <CForm className="row g-3" onSubmit={handleSubmit}>
                <CCol xs={12}>
                  <CFormLabel htmlFor="inputAddress">State</CFormLabel>
                  <CFormInput
                    id="inputAddress"
                    name="name"
                    value={singleState}
                    onChange={(e) => setSingleState(e.target.value)}
                    type="text"
                    required
                    placeholder="State name"
                  />
                </CCol>
                <CCol xs={12} className="text-center">
                  <CButton type="submit" className="bg-black text-white border-b ">
                 Submit
                  </CButton>
                </CCol>
              </CForm>
            </CCardBody>
          </CCard>
        </CCol>
      </div>
    </>
  );
};

export default EditState;
